@import '~antd/dist/antd.css';

.App {
  text-align: center;
  height:100vh
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.ant-layout-sider-children{
  background-color:#f8f4ed;
  padding-top: 5px;
  margin-top: 0;
  height: 95%;
  overflow-y: scroll;
  overflow-x: hidden;
}
.ant-layout-sider-children::-webkit-scrollbar{
  width: 5px;
}
.ant-layout-sider-children::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background-color: #ececec
}
.ant-layout-sider-children::-webkit-scrollbar-track {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: #f8f4ed
}

.ant-menu-item{
  text-align: center !important;
  padding-left: 0 !important;
}
.ant-menu-submenu-title{
  font-size: 15px !important;
  padding-left: 0px !important;
  height:50px !important;
  line-height: 50px !important;
}

.ant-drawer-content-wrapper{
  width: 300px !important;
}
.ant-drawer-body{
  padding-bottom: 60px;
}
.ant-drawer-wrapper-body::-webkit-scrollbar{
  width: 5px;
}
.ant-drawer-wrapper-body::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background-color: #ececec
}
.ant-drawer-wrapper-body::-webkit-scrollbar-track {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: #f8f4ed
}
.ant-btn-background-ghost{
  color:#757575;
  border-color: #757575;
}
.esri-component.esri-search.esri-widget{
  position: absolute;
  right:20px;
  top:20px
}
.esri-component.esri-compass.esri-widget--button.esri-widget.esri-interactive{
  position: absolute;
  left:30px;
  top:20px
}
.esri-component.esri-zoom.esri-widget{
  position: absolute;
  left:80px;
  top:20px
}
.esri-component.esri-scale-bar.esri-widget{
  position: absolute;
  right:60px;
  bottom:20px
}
.ant-timeline{
  position: absolute;
  top:15%;
  left:90px;
  padding:20px;
  width: 400px;
  height:500px;
  background-color: rgb(240,240,240,0.5);
  text-align: justify;
  overflow-y: auto
}
.ant-timeline-item-content:hover{
  color: deepskyblue;
  cursor: pointer;
  transition: 0.2s;
  transition-duration:0.5s;
}
.ant-timeline-item-head.ant-timeline-item-head-blue{
  border-color: #63bbd0;
}
.ant-timeline-item-head.ant-timeline-item-head-custom.ant-timeline-item-head-blue{
  background-color: rgba(0,0,0,0.0);
}
.ant-timeline-item.ant-timeline-item-last{
  padding-bottom: 0
}
.ant-timeline::-webkit-scrollbar{
  width: 5px;
}
.ant-timeline::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background-color: #ececec
}
.ant-timeline::-webkit-scrollbar-track {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0);
  background-color: #f8f4ed
}
.ant-timeline-item-tail{
  background-color: #d6d6d6;
  width: 4px;
  left:2px
}
.charExp{
  font-size: 25px;
  padding-bottom:20px;
}

.tool_item_text {
  padding: 30px;
}

.toolbox_item {
  display: flex;
  justify-content: space-between;
}

.ant-btn.ant-btn-primary{
  background-color: #0f95b0;
  border-color: #0f95b0
}
.ant-btn.ant-btn-primary:hover{
  background-color: #0f95b0b0;
  border-color: #0f95b0b0
}
.ant-switch.ant-switch-checked{
  background-color: #0f95b0;
}